<template>
    <div class='swiper-container'>
        <div class='swiper-wrapper'>
            <div class='swiper-slide ddd' v-for='data in swiper1' :key='data.name'>
                <img :src='data.avatarAddress' class='bbb' />
                <span class='na'>{{data.name}}</span>
                <span class='role'>{{data.role}}</span>
            </div>
        </div>
   </div>
</template>
<script>
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
export default {
  mounted () {
    /* eslint-disable no-new */
    new Swiper('.swiper-container', {
      slidesPerView: 4,
      spaceBetween: 20,
      freeMode: true
    })
  },
  props: ['swiper1']
}
</script>
<style lang="scss" scoped>
    .swiper-wrapper{
        display: flex;
        justify-content: space-start;
        padding: 0.1rem 0.2rem;
        .ddd{
            width: 1.2rem;
            margin-right: 0;
            .bbb{
                display: block;
                width: 0.85rem;
                height: 1.1rem;
            }
            span{
                width: 100%;
                display: block;
                font-size: 0.16rem;
                margin-left: 0.1rem;
            }
            .na{
                margin-top: 0.1rem;
            }
            .role{
                font-size: 10px;
                color: #797d82;
            }
        }
    }
</style>
