<template>
     <div class='swiper-container fff'>
        <ul class='swiper-wrapper'>
            <li v-for='(data,index) in swiper2' :key='index' class='swiper-slide'>
                <img :src='data' />
            </li>
        </ul>
    </div>
</template>
<script>
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
export default {
  mounted () {
    /* eslint-disable no-new */

    new Swiper('.fff', {
      slidesPerView: 3,
      spaceBetween: 20,
      freeMode: true
      // loop: true
    })
  },
  props: ['swiper2']
}
</script>
<style lang="scss" scoped>
.swiper-wrapper{
  padding-left: 0.2rem;
}
.swiper-slide{
  width: 2rem!important;
  // height: 2.4rem;
  img{
    width: 100%;
    max-height: 1.5rem;
  }
}

</style>
