<template>
    <div class='detail'>
        <div class='scoll' v-if='isTopshow'>
            {{this.filmslist.name}}
        </div>
        <div class='poster'>
            <img :src='this.filmslist.poster' />
            <router-link :to='this.isSale' tag='div' activeClass='return'>
                <img src='../../public/return.png' class='return'/>
            </router-link>
        </div>
        <div class='infomation'>
            <div class='f1'>
                <span class='haha'>{{this.filmslist.name}}</span>
                <span class='item'>{{this.filmtype}}</span>
            </div>
            <div class='f1'>
                <span class='label'>{{this.filmslist.category}}</span>
            </div>
            <div class='f1'>
                <span class='label'>{{this.filmslist.premiereAt}}上映</span>
            </div>
            <div class='f1'>
                <span class='label'>{{this.filmslist.nation}} | 分钟</span>
            </div>
            <div class='f2'>
                <div class='synopsis'>{{this.filmslist.synopsis}}</div>
            </div>
        </div>
        <div class='actors'>
            <div class='f3'>
                <div class='title'>演职人员</div>
            </div>
            <div class='actorslist'>
                <swiper1 :swiper1='this.filmslist.actors' :key='this.actorslength'></swiper1>
            </div>
        </div>
        <div class='imgs'>
            <div class=f4>
                <div class='imgsname'>
                    <div class='i1'>剧照</div>
                    <div class='i2'>全部({{this.filmlength}})></div>
                </div>
            </div>
            <div class='imgslist'>
                <swiper2 :swiper2='this.filmslist.photos' :key='this.filmlength'></swiper2>
            </div>
        </div>
        <div class='buy' v-if='isShow'>
            选座购票
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import swiper1 from '@/views/swiper1'
import swiper2 from '@/views/swiper2'
export default {
  data () {
    return {
      isSale: '',
      isShow: '',
      isTopshow: false,
      filmslist: [],
      id: '',
      url: 'https://m.maizuo.com/gateway?filmId=',
      url2: '',
      filmtype: '',
      filmlength: '',
      actorslength: ''
    }
  },
  components: {
    swiper1,
    swiper2
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
    this.id = parseInt(this.$route.params.id)
    // console.log(this.id)
    this.url2 = this.url + this.id + '&k=6744997'
    axios({
      url: this.url2,
      method: 'get',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1582288622450971566184","bc":"310100"}',
        'X-Host': 'mall.film-ticket.film.info'
      }
    }).then(res => {
      this.filmslist = res.data.data.film
      this.filmtype = this.filmslist.filmType.name
      this.filmlength = this.filmslist.photos.length
      this.actorslength = this.filmslist.actors.length
      // 根据是否上映来选择详情页返回的页面是上映还是在映
      if (this.filmslist.isSale) {
        this.isSale = '/films/nowPlaying'
        this.isShow = true
      } else {
        this.isSale = '/films/comingSoon'
        this.isShow = false
      }
    })
  },
  methods: {
    handleScroll () {
      //   实时获取滚动条距顶部的距离。
      // console.log(document.documentElement.scrollTop)
      if (document.documentElement.scrollTop >= 50) {
        this.isTopshow = true
      } else {
        this.isTopshow = false
      }
    }
  }
}
</script>
<style lang="scss">
    .detail{
        width: 100%;
        .scoll{
            width: 100%;
            height: 0.8rem;
            background:white;
            font-size: 0.24rem;
            text-align: center;
            line-height: 0.8rem;
            position: fixed;
            left: 0;
            right: 0;
            top: 0;
        }
        .poster{
            width: 100%;
            // background: red;
            img{
                display: block;
                height: 3.5rem;
                width: 100%;
            }
            .return{
                width: 0.5rem;
                height: 0.5rem;
                position: fixed;
                left: 0;
                top: 0.1rem;
            }
        }
        .infomation{
            height: 2.5rem;
            box-sizing: border-box;
            padding: 0.12rem 0.15rem 0.15rem;
            background: white;
            border-bottom: 0.1rem solid #eeeeee;
            overflow: hidden;
            .f1{
                display: flex;
                justify-content:flex-start;
                margin: 0.04rem 0 0;
                .item{
                    font-size: 0.09rem;
                    color: #fff;
                    background-color: #d2d6dc;
                    height: 0.14rem;
                    line-height: 0.14rem;
                    padding: 0 0.02rem;
                    border-radius: 0.02rem;
                    display: inline-block;
                }
                .haha{
                    color: #191a1b;
                    font-size: 0.24rem;
                    height: 0.24rem;
                    line-height: 0.24rem;
                    margin-right: 0.07rem;
                    }
                .label{
                    font-size: 0.16rem;
                    color: #797d82;
                    margin-top: 0.04rem;
                }
            }
            .f2{
                .synopsis{
                    height: 0.8rem;
                    font-size: 0.17rem;
                    line-height: 0.26rem;
                    margin-top: 0.12rem;
                    color: #797d82;
                    overflow: hidden;
                }
            }
        }
        .actors{
            height: 2.5rem;
            .f3{
                padding: 0.15rem;
                height: 0.3rem;
                // border-bottom: 0.1rem solid #eeeeee;
                .title{
                    font-size: 0.24rem;
                    text-align: left;
                    color: #191a1b;
                    line-height: 0.3rem;
                }
            }
        }
        .imgs{
            height: 2rem;
            border-top: 0.1rem solid #eeeeee;
            .f4{
                padding: 0.15rem;
                height: 0.4rem;
               .imgsname{
                   height: 0.3rem;
                   display: flex;
                   justify-content:space-between;
                   .i1{
                       font-size: 0.16rem;
                       line-height: 0.3rem;
                   }
                   .i2{
                       font-size: 0.13rem;
                       line-height: 0.3rem;
                   }
               }
            }
        }
        .buy{
            font-size: 0.24rem;
            line-height: 0.6rem;
            text-align: center;
            color: white;
            height: 0.6rem;
            position: fixed;
            left: 0;
            right: 0;
            bottom: 0;
            background: #ff5f16;
            z-index: 5;
        }
    }
</style>
